import React, {useState} from 'react';
//import logo from './logo.svg';
import './App.css';

// declare global {
//   interface Window {
//     showOpenFilePicker:any;
//   }
// }

function App() {

  const [text, setText]: [string, React.Dispatch<React.SetStateAction<string>>] = useState("TEST");

  async function onClick() {
    let fileHandle: FileSystemFileHandle;
    [fileHandle] = await window.showOpenFilePicker();
    const file: File = await fileHandle.getFile();
    console.log(file);
    const contents: string = await file.text();
    setText(contents);
  }

  async function onClick2() {
    const dirHandle = await window.showDirectoryPicker();
    for await (const entry of dirHandle.values()) {
      console.log(entry.name);
      if(entry.kind === "directory") {
        let dirHandle2 = await dirHandle.getDirectoryHandle(entry.name);
        for await (const entry2 of dirHandle2.values()){
          console.log(entry2.name)
          //Assets like a picture can also be here.
          if(entry2.kind === "file"){
            let fileHandle: FileSystemHandle = await dirHandle2.getFileHandle(entry2.name);
            let file: File = await fileHandle.getFile();
            console.log(await file.text());
          }
        }
      }
    }
  }

  return (
    <div className="App">
      <header className="App-header">
        <div
          onClick={onClick2}
        >
          Button
        </div>
        <div>{text}</div>
      </header>
    </div>
  );
}

export default App;
